import { Grid } from "./Grid";

const submitWordsBtn = document.querySelector(".submitWords");



submitWordsBtn.addEventListener("click", async () => {
    const grid = new Grid();
    const commaSeparatedWords = document.querySelector("#WordsTextBox").value;
    const gridSize = document.querySelector("#grid-size").value;

    let result = await fetchGridInfo(gridSize, commaSeparatedWords.split(' ').join('').toUpperCase());
    grid.words = commaSeparatedWords.split(' ').join('').toUpperCase().split(",");
    grid.renderGrid(gridSize, result);
    let wordListNode = document.createTextNode(grid.words); 
    let wordListSection = document.querySelector(".word-list");
    while(wordListSection.lastChild){
        wordListSection.removeChild(wordListSection.lastChild);
    }  
    wordListSection.appendChild(wordListNode);

    
    let foundWordsListNode = document.createTextNode(grid.foundWords);
    let foundWordsListSection = document.querySelector(".found-words");
    if(foundWordsListSection.lastChild){
        foundWordsListSection.removeChild(foundWordsListSection.lastChild);
    }
    foundWordsListSection.appendChild(foundWordsListNode);

});

async function fetchGridInfo(gridSize, commaSeparatedWords){
    let response = await fetch(`http://localhost:8080/wordgrid?gridSize=${gridSize}&stringOfWords=${commaSeparatedWords}`);
    let result = await response.text();
    return result.split(" ");
}